.Controls {
    display: flex;
    flex-direction: column;
    width: 30%;
    align-self: center;
    border: 1px solid black;
    border-radius: 4px;
    padding: 16px 8px;
    background: white;
    transition: 0.5s ease;
}

.VisualizeButton {
    background: #0ad792;
    border: 1px solid black;
    border-radius: 4px;
    margin: 8px 0;
    cursor: pointer;
    padding: 8px;
    outline: none;
    transition: 0.3s ease;
}

.Hide {
    opacity: 0;
    visibility: hidden;
}

.Visualizing {
    opacity: 0;
}

h3 {
    margin: 0;
}

.TitleContainer {
    margin: 16px 0;
}

.ButtonRow {
    display: flex;
    justify-content: space-evenly;
}

.Button {
    background: transparent;
    border: 1px solid black;
    border-radius: 4px;
    margin: 8px 0;
    cursor: pointer;
    padding: 8px;
    outline: none;
    transition: 0.3s ease;
}

.Button:hover {
    background: #f3f1f1;
}

.VisualizeButton:hover {
    background: #0fc48a;
}
